.profileInfoBlock {
    display: flex;
    flex-direction: row;
    padding: 10px;
}
.mainPhoto {
    max-width: 250px;
}
.profileDataBlock {
    flex-direction: column;
    padding-left: 10px;
}
.profileName span{
    margin-top: 0;
    font-size: 30px;
}
.profileStatus {
    margin-top: 10px;
    margin-bottom: 20px;
}
.infoPoint span{
    font-weight: bold;
}
.contact {
    padding-left: 10px;
}
.formSummaryError {
    border: red 1px solid;
    padding: 5px;
    color: red;
}